.profile {
  padding-top: rem(100);
  background-color: #f3f4f8;

  &_header {
    border-bottom: rem(1) solid $orange;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;

    &_left {
      padding: rem(10) 0;

      h2 {
        @include estiprotextbook(24);
        font-weight: normal;
        color: $orange-second;
      }
    }

    &_right {
      position: relative;

      &:hover {
      }

      &_url {
        color: #9e9fa1;
        @include estiprotext(13);
        font-weight: 400;
        border-bottom: rem(1) solid $orange;
        padding: rem(13) 0;

        span {
          padding-right: rem(5);

          &:before {
            color: $orange;
            font-size: rem(18);
          }
        }
      }
    }
  }

  &_pad {
    padding: rem(20) 0 rem(40);
  }

  &_content {
    background-color: $white;
    border-radius: rem(10);
    box-shadow: 0px 2px 5px 0 rgba(0, 0, 0, 0.14);
    padding: rem(15) rem(20);
    display: flex;

    &_left {
      &_top {
        display: flex;
        padding-bottom: rem(15);
        align-items: center;

        &_img {
          border-radius: 50%;
          background-color: #bae3f9;
          color: #a3c7db;
          display: flex;
          align-items: center;
          justify-content: center;
          max-width: rem(65);
          min-width: rem(65);
          max-height: rem(65);
          min-height: rem(65);
          width: 100%;
          height: 100%;

          span {
            &:before {
              font-size: rem(21);
            }
          }
        }

        &_name {
          padding-left: rem(10);
          max-width: rem(145);

          h2 {
            @include estiprotext(20);
            font-weight: 500;
            color: $gray-dark;
          }

          p {
            @include estiprotext(16);
            color: $gray-dark;
            font-weight: normal;
            margin-top: rem(5);
          }
        }
      }

      &_transition {
        border-top: rem(1) solid $gray-light;

        &_nav {
          display: block;

          &_item {
          }
        }

        &_url {
          display: block;
          position: relative;
          padding: 0 rem(5);
          @include estiprotext(16);
          font-weight: normal;
          line-height: 2.5;
          color: $gray-dark;
          white-space: nowrap;

          &:hover {
            background-color: rgba(201, 201, 201, 0.2);
          }

          &.active {
            background-color: rgba(201, 201, 201, 0.2);
          }

          .profile_left_icon {
            padding-right: rem(10);

            &:before {
              color: $orange;
            }
          }

          .icon-orders {
            &:before {
              font-size: rem(16);
            }
          }

          .icon-profile_menu, .icon-envelope {
            &:before {
              font-size: rem(11);
            }
          }

          .icon-settings {

            &:before {
              font-size: rem(16);
            }
          }

          .notification {
            display: none;
            margin-left: rem(5);
            color: $white;
            @include estiprotext(10);
            width: rem(12);
            height: rem(12);
            border-radius: 50%;
            background-color: #22ac66;
            align-items: center;
            justify-content: center;
          }

          &_message {
            .notification {
              display: inline-flex;
            }
          }
        }
      }
    }

    &_right {
      width: 100%;
      padding-left: rem(55);

      &_header {
        display: flex;
        align-items: center;
        justify-content: flex-end;

        &_price {
          padding-top: rem(15);
          text-align: right;
          padding-right: rem(15);

          p {
            @include estiprotext(13);
            font-weight: normal;
            color: #9e9fa1;
            margin-bottom: rem(5);
          }

          h4 {
            @include estiprotext(15);
            font-weight: normal;
            color: #262626;
          }
        }

        &_btn {
          padding-top: rem(15);
          border-radius: rem(16.5);
          max-width: rem(150);
          width: 100%;

          .btn {
            @include estiprotext(16);
            font-weight: normal;
          }
        }

        &_btn_info {
          padding-top: rem(15);
          padding-left: rem(10);

          span {
            &:before {
              font-size: rem(23);
              color: #a9cc8c;
            }
          }
        }
      }

      &_tabs {
        &_header {
          &_nav {
            &_item {
              padding-right: rem(35);

              &_link {
                &.active {
                  color: $gray-dark;
                  border-bottom: rem(1) solid $gray-light;
                }

                padding: 0 0 rem(5);
                @include estiprotext(16);
                font-weight: normal;
                color: #b8b8b8;
              }
            }
          }
        }

        &_content {
          &_block {
            display: flex;
            align-items: center;
            border-top: rem(1) solid $gray-light;

            &_img {
              max-width: rem(117);
              max-height: rem(80);
              width: 100%;
              height: 100%;
            }

            &_info {
              padding: rem(15);

              &_add {
                color: #8f8f8f;
                display: flex;
                align-items: center;

                p {
                  @include estiprotext(12);
                }

                span {
                  padding: 0 rem(5);
                }
              }

              h3 {
                @include estiprotext(17);
                font-weight: normal;
                color: $gray-dark;
                margin: rem(10) 0;
                text-align: left;
              }

              &_message {
                color: #9e9fa1;
                display: flex;
                align-items: center;

                &_img {
                  position: relative;
                  display: flex;

                  .icon-envelope {
                    &:before {
                      font-size: rem(14);
                      color: #c2c2c2;
                    }
                  }

                  .notification {
                    position: absolute;
                    top: rem(-5);
                    right: rem(-5);
                    color: $white;
                    @include estiprotext(10);
                    width: rem(12);
                    height: rem(12);
                    border-radius: 50%;
                    background-color: #22ac66;
                    align-items: center;
                    justify-content: center;
                  }
                }

                p {
                  @include estiprotext(13);
                  font-weight: normal;
                  margin-left: rem(10);
                }
              }
            }

            &_edit {
              margin-left: auto;
              padding: rem(8) 0;

              &_url {
                display: block;
                text-align: left;
                @include estiprotext(13);
                font-weight: normal;
                color: $gray-dark;
                padding: rem(8) 0;

                &:hover {
                  color: $orange;
                }

                span {
                  padding-right: rem(13);
                  font-size: rem(13);
                }

                .icon-delete {
                  padding-right: rem(15);
                }
              }
            }

            &_right {
              margin-left: rem(60);
              padding: rem(10) 0;

              &_market {
                padding-bottom: rem(25);

                .btn {
                  max-width: rem(130);
                  border-radius: rem(16);
                  border: solid rem(2) rgb(194, 194, 194);
                  color: rgb(194, 194, 194);
                  @include estiprotext(14);
                  font-weight: normal;
                  background-color: rgba(253, 151, 6, 0);
                }

                .btn-orange {
                  color: #fe9b19;
                  border: solid rem(2) #fe9b19;
                }

                .btn-gray {
                  border: solid rem(2) rgb(194, 194, 194);
                  color: rgb(194, 194, 194);

                  &:hover {
                    background-color: rgb(194, 194, 194);
                    color: $white;
                  }
                }
              }

              &_view {
                display: flex;
                align-items: center;

                span {
                  display: flex;
                  align-items: center;

                  &:before {
                    font-size: rem(9);
                    opacity: 0.45;
                    color: $black;
                  }
                }

                p {
                  margin-left: rem(5);
                  @include estiprotext(14);
                  font-weight: normal;
                  color: $gray-dark;
                }
              }
            }
          }
        }
      }
    }
  }

  &_setting {
    &_header {
      display: flex;

      p {
        @include estiprotext(16);
        padding-bottom: rem(5);
        color: $gray-dark;
        border-bottom: .0625rem solid $gray-light;
      }
    }

    &_contact {
      display: flex;
      border-top: rem(1) solid $gray-light;
      padding: rem(25) 0 rem(5);

      &_header {
        width: 25%;

        h4 {
          line-height: 1.2;
          @include estiprotext(16);
          color: $black;
        }
      }

      &_right {
        width: 65%;

        &_input {
          display: flex;
          align-items: center;
          padding-bottom: rem(20);

          .registr_content_left_form_row_input {
            max-width: rem(345);
            width: 100%;
          }

          .select2 {
            width: 100% !important;
          }

          .select2-container--default .select2-selection--single {
            background-color: #c4c4c4;
            opacity: 1;
            border: rem(1) solid #626262;

            .select2-selection__rendered{
              opacity: 0.8;
              color: #535353;
            }
          }

          input {
            display: block;
            max-width: rem(345);
            width: 100%;
            min-height: rem(50);
            max-height: rem(50);
            height: 100%;
            background: #fff;
            border: rem(1) solid #626262;
            box-sizing: border-box;
            border-radius: rem(2);
            padding-left: rem(18);
            color: $gray-dark;
            @include estiprotext(16);
          }

          label {
            margin: 0;
            margin-left: rem(25);
            @include estiprotext(16);
            font-weight: normal;
            color: rgba(83, 83, 83, 0.6);
          }

          .registr_content_left_form_row_input_datepicker {
            margin: 0;
          }
        }

        fieldset {
          input {
            background-color: #c4c4c4;
            color: rgba(83, 83, 83, 0.8);
          }
        }

        &_btn {
          padding: rem(5) 0 rem(10);
          max-width: rem(345);
          width: 100%;
          display: flex;
          justify-content: flex-end;

          .btn {
            min-width: rem(150);
          }
        }
      }
    }

    &_favour {
      border-top: rem(1) solid $gray-light;
      display: flex;
      align-items: center;
      padding: rem(15) 0;

      .content_tours_block_center_navigation {
        padding: rem(5) 0 rem(10);
      }

      &_img {
      }

      &_center {
      }

      &_right {
        max-width: 11.25rem;
        width: 100%;
        margin-left: auto;

        &_top {
          .content_tours_block_right_price {
            padding: 0;
          }
        }

        &_delete {
          margin-left: auto;
          padding: rem(10) 0 0;

          &_url {
            display: block;
            text-align: left;
            font: .8125rem GT Eesti Pro Text, sans-serif;
            font-weight: 400;
            color: rgba(83, 83, 83, .7);
            padding: .5rem 0;
            text-align: right;

            &:hover, &:focus {
              color: orange;
            }
          }
        }
      }
    }
  }

  &_message {
    display: flex;
    align-items: center;
    border-top: rem(1) solid $gray-light;

    &_from {
      display: flex;
      align-items: center;

      span {
        &:before {
          color: rgb(158, 159, 161);
        }
      }

      .icon-from-out {
        transform: scale(-1, 1);
      }

      p {
        margin-left: rem(10);
        @include estiprotext(14);
        color: rgb(158, 159, 161);
        line-height: 1.2;
      }
    }

    &_name {
      display: flex;
      align-items: center;
      padding: rem(15) rem(35);

      &_nav {
        &_item {
          &_link {
            padding: 0;
          }
        }
      }

      a {
        @include estiprotext(17);
        font-weight: normal;
        color: $gray-dark;
        margin: rem(10) rem(15);
        text-align: left;

        &:hover {
          color: $orange;
        }
      }

      &_active {
        a {
          font-weight: bold;
          line-height: 1.2;
        }
      }

      .notification {
        color: $white;
        @include estiprotext(14);
        width: rem(21);
        height: rem(21);
        border-radius: 50%;
        background-color: #22ac66;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    &_edit {
      margin-left: auto;
      padding: rem(8) 0;

      &_url {
        display: block;
        text-align: left;
        @include estiprotext(13);
        font-weight: normal;
        color: $gray-dark;
        padding: rem(8) 0;

        &:hover {
          color: $orange;
        }

        span {
          padding-right: rem(15);
          font-size: rem(13);
        }
      }
    }

    &_right {
      margin-left: rem(125);
      padding: rem(10) 0;

      p {
        margin-left: rem(5);
        @include estiprotext(12);
        font-weight: normal;
        color: #9e9e9e;
        line-height: 1.2;
        text-align: right;
      }
    }
  }

  &_chat {
    border-top: rem(1) solid $gray-light;

    &_header {
      display: flex;
      align-items: center;
      background-color: rgba(201, 201, 201, 0.2);
      border-bottom: rem(1) solid rgb(201, 201, 201);

      &_from {
        display: flex;

        a {
          margin-left: rem(10);
          @include estiprotext(14);
          color: rgb(158, 159, 161);
          line-height: 1.2;
          display: flex;
          align-items: end;

          &:hover {
            color: $orange;
          }

          span {
            transform: rotate(90deg);

            &:before {
              font-size: rem(8);
            }
          }
        }
      }

      &_name {
        display: flex;
        align-items: center;
        padding: 0 rem(35);

        a {
          @include estiprotext(17);
          font-weight: normal;
          color: $gray-dark;
          margin: rem(10) rem(15);
          text-align: left;

          &:hover {
            color: $orange;
          }

          span {
            &:before {
              font-size: rem(12);
              color: $orange;
            }
          }
        }

        &_active {
          a {
            font-weight: bold;
            line-height: 1.2;
          }
        }

        .notification {
          color: $white;
          @include estiprotext(14);
          width: rem(21);
          height: rem(21);
          border-radius: 50%;
          background-color: #22ac66;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      &_edit {
        margin-left: auto;
        padding: rem(8) rem(15);

        &_url {
          display: block;
          text-align: left;
          @include estiprotext(13);
          font-weight: normal;
          color: $gray-dark;
          padding: rem(8) 0;

          &:hover {
            color: $orange;
          }

          span {
            padding-right: rem(15);
            font-size: rem(13);
          }
        }
      }
    }

    &_content {
      display: flex;
      flex-direction: column;
      padding: rem(17.5) 0 rem(10);

      &_client {
        width: 67%;
        align-self: flex-end;

        &_content {
          background-color: rgba(34, 172, 102, 0.25);
        }
      }

      &_client, &_own {
        padding: rem(7.5) 0;

        &_header {
          padding: rem(5) 0;

          h5 {
            @include estiprotext(14);
            color: rgb(158, 159, 161);
            line-height: 1.2;
            text-align: left;
          }
        }

        &_content {
          border-radius: rem(6);
          padding: rem(15);
          padding-left: rem(20);

          p {
            @include estiprotext(17);
            color: rgb(83, 83, 83);
            line-height: 1.2;
            text-align: left;
          }
        }

        &_date {
          padding: rem(5) 0;

          p {
            @include estiprotext(12);
            color: rgb(143, 143, 143);
            line-height: 1.2;
            text-align: right;
          }
        }
      }

      &_own {
        width: 70%;
        align-self: flex-start;

        &_content {
          background-color: rgba(186, 227, 249, 0.5);
        }
      }

      &_answer {
        margin-top: rem(17.5);
        padding: rem(25) rem(23) rem(20);
        background-color: rgba(186, 227, 249, 0.5);

        &_text-area {
          position: relative;

          textarea {
            width: 100%;
            padding: rem(17) rem(15);
            border-radius: rem(2);
            border: solid rem(1) #c4c4c4;
            font-family: GTEestiProText;
            font-size: 16px;
            @include estiprotext(16);
            font-weight: normal;
            color: $gray-dark;
          }

          .max-words {
            position: absolute;
            right: rem(10);
            bottom: rem(10);
            @include estiprotext(13);
            font-weight: normal;
            color: rgba(83, 83, 83, 0.4);
          }
        }

        &_bottom {
          display: flex;
          justify-content: space-between;
          padding-top: rem(5);

          &_file {
            position: relative;
            text-align: left;

            input {
              opacity: 0;
              width: 100%;
              height: 100%;
              position: absolute;
              z-index: -1;
            }

            label {
              span {
                padding-right: rem(5);

                &:before {
                  font-size: rem(12);
                }
              }

              cursor: pointer;
              @include estiprotext(14);
              color: rgb(83, 83, 83);
              line-height: 1.2;
              margin: 0;
            }

            p {
              @include estiprotext(12);
              color: rgb(143, 143, 143);
              line-height: 1.2;
            }
          }

          &_url {
            max-width: rem(150);
            width: 100%;

            .btn {
            }
          }
        }
      }
    }
  }

  &_corporate {
    &_chat {
      &_content {
        &_client {
          width: 67%;
          align-self: flex-start;
          .profile_chat_content_client_content{
            background-color: rgba(186, 227, 249, 0.5);
          }
        }

        &_own {
          width: 70%;
          align-self: flex-end;
          .profile_chat_content_own_content {
            background-color: rgba(34, 172, 102, 0.25);
          }
        }
      }
    }

    &_right_select {
      &_dis {
        .select2-container--default
        .select2-selection--single
        .select2-selection__rendered {
          color: rgba(83, 83, 83, 0.8);
        }
      }

      .select2-container--default
      .select2-selection--single {
        background-color: transparent;

        .select2-selection__placeholder {
          color: $gray-dark;
        }
      }
    }
    &_inbox{
      &_header{
        border-bottom: none;
      }
      &_left{
        img + img{
          padding-left: rem(2);
        }
      }
    }
  }

  &_quantity {
    display: flex;
    align-items: center;

    &_btn {
      display: flex;
      align-items: center;
      padding-left: rem(25);

      a {
        width: rem(12);
        height: rem(12);
        border-radius: 50%;
        background-color: rgb(194, 194, 194);
        display: flex;
        align-items: center;
        justify-content: center;
        color: $white;
        font-size: rem(10);

        &:hover, &:focus {
          color: $white;
          background-color: $orange;
        }
      }

      p {
        @include estiprotext(16);
        color: rgb(82, 82, 82);
        margin: 0 rem(5);
      }
    }

    &_info {
    }
  }
}

.profile_left_icon {
  padding-right: rem(10);

  &:before {
    color: $orange;
  }
}

::-webkit-input-placeholder {
  color: rgba(83, 83, 83, 0.702);
}

::-moz-placeholder {
  color: rgba(83, 83, 83, 0.702);
}

/* Firefox 19+ */
:-moz-placeholder {
  color: rgba(83, 83, 83, 0.702);
}

/* Firefox 18- */
:-ms-input-placeholder {
  color: rgba(83, 83, 83, 0.702);
}

.select2-results__option .wrap {
  position: relative;
  padding-left: rem(30);

  &:before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 18px;
    height: 18px;
    padding-right: rem(10);
    border-radius: rem(2);
    border: solid rem(1) #c4c4c4;

  }

}

.select2-results__option[aria-selected=true] .wrap:after {
  content: "";
  background: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3E%3Cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-color: $orange;
  position: absolute;
  left: rem(3);
  width: rem(11);
  height: rem(11);
  top: 50%;
  transform: translateY(-50%);
}

.select2-search--dropdown {
  display: none;
}


.select2-results__option[aria-selected=true] .wrap:before {
  background-color: $orange;
}

/* not required css */

.select2-multiple, .select2-multiple2 {
  width: 50%
}