.header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 6;

  &_navbar {
    padding: 0;
    position: relative;

    &_brand {
      padding: rem(5) 0;

      &_logo {
        padding: 0 0 rem(11);
      }
    }

    &_collapse {
      &.show {
        position: fixed;
        width: 100%;
        height: 100vh;
        top: 0;
        left: 0;
        z-index: 99;

        .header_navbar_collapse_absolute {
          background-color: rgba(0, 0, 0, 0.75);
          width: 100%;
          height: 100%;
          position: absolute;
        }

        .active {
          .header_navbar_collapse_nav_item_link {
            color: $white;
          }
        }

        .header_navbar_collapse_nav {
          margin: 0;
          width: rem(330);
          height: 100%;
          background-color: $orange;
          position: absolute;
          top: 0;
          right: 0;

          &_item {
            border-bottom: rem(1) solid $white;

            &_pad {
              padding: rem(15) 0;

              &:before {
                display: none;
              }
            }
          }
        }

        .header_navbar_collapse_nav_item_link {
          color: $white;
        }
      }

      &_nav {
        &_close {
          display: none;
          position: absolute;
          width: rem(27);
          height: rem(26);
          right: rem(20);
          top: rem(25);

          &_btn {
            border: none;
            background-color: transparent;
            font-size: rem(45);
            margin-top: rem(-22);
            font-weight: bold;
            color: $white;
          }
        }

        &_item {
          padding: 0 rem(16);
          display: flex;
          align-items: center;

          &_white {
            .header_navbar_collapse_nav_item_link {
              color: $white;

              &:hover, &:focus {
                color: $purple;
              }
            }
          }

          &_pad {
            position: relative;
            padding: rem(33) 0;
          }

          &_link {
            padding: 0;
            @include estiprotextbook(16);
            font-weight: 400;
            color: #25252f;

            &:hover, &:focus {
              color: #fe9b19;
            }
          }
        }

        &_pad-btn {
          padding-left: rem(55);

          .btn {
            border: rem(2) solid $purple;
            background-color: $purple;
            color: $white;
            min-width: rem(135);
            width: 100%;

            &:hover, &:focus {
              background-color: $purple;
              box-shadow: 0 4px 10px rgba(90, 0, 147, 0.91);
              color: $white;
            }
          }
        }
      }
    }

    &_lang {
      &_menu_link {
        color: $white;
        font-weight: 250;
        line-height: 22px;
        text-transform: uppercase;
        padding: 0;
        display: flex;
        align-items: center;

        &:hover, &:focus {
          color: $white;
        }

        &:after {
          display: inline-block;
          font-size: rem(3);
          margin-left: rem(5);
          content: "\e903";
          font-family: icomoon !important;
          speak: none;
          font-style: normal;
          font-variant: normal;
        }
      }

      &_menu_dropdown {
        min-width: auto;
        border-radius: rem(2);
        margin-top: rem(8);
        padding: 0;
        box-shadow: 0 rem(2) rem(5) rgba(0, 0, 0, 0.4);

        &_item {
          @include estiprotext(14);
          color: rgba(52, 52, 59, 0.702);
          line-height: 1.2;
          padding: rem(9) rem(23);

          &:hover {
            background-color: #f3f3f3;
            color: $orange;
          }
        }
      }
    }

    &_profile {
      padding-right: rem(25);

      .btn {
        background-color: $white;
        border-radius: 50%;
        min-width: rem(28);
        width: 100%;
        min-height: rem(28);
        height: 100%;
        padding: 0;

        .icon-profile {
          color: #a04800;
          opacity: 0.6;
        }

        &:hover, &:focus {
          background-color: $white;

          .cls-1 {
            fill: $purple;
            fill-rule: evenodd;
            opacity: 0.6;
          }
        }
      }
    }
  }
}