@import "variables";

//Methods

@function rem($pixels, $context: $browser-context) {
    @return #{$pixels/$context}rem;
}

//Mixin

@mixin magistral($rem){
    font: rem($rem) $magistral;
    //ответ
    //@include magistral(35);
}

