.registr {
  padding-top: rem(80);
  background-color: #f3f4f8;

  &_header {
    padding: rem(35) 0 rem(5);
    border-bottom: rem(1) solid $orange;

    h2 {
      color: #ff9000;
      @include estiprotextbook(24);
      font-weight: 400;
    }
  }

  &_pad {
    padding: rem(20) 0 rem(40);
  }

  &_content {
    background-color: $white;
    display: flex;
    align-items: center;
    box-shadow: 0 rem(2) rem(5) rgba(0, 0, 0, 0.14);
    border-radius: rem(10);
    overflow: hidden;

    &_left {
      padding: rem(45) rem(27.5) rem(30);
      width: rem(830);

      &_form {
        width: 100%;

        &_row {
          width: 100%;

          .mb-3 {
            margin-bottom: rem(25) !important;
            padding: 0 rem(17.5) !important;
          }

          &_input {
            position: relative;

            &.form-input-log {
              span {
                path {
                  stroke: #CECECE;
                  fill: none;
                }
              }

              &:hover {
                span {
                  path {
                    stroke: $orange-second;
                    fill: none;
                  }
                }
              }
            }

            &:hover {
              span {
                path {
                  fill: $orange-second;
                }
              }
            }

            .select2-container {
              &--default {
                &.select2-container--open {
                  .select2-selection--single {
                    .select2-selection__arrow {
                      b {
                        transform: rotate(180deg);
                      }
                    }
                  }
                }

                .select2-selection--single {
                  display: flex;
                  align-items: center;
                  border-radius: rem(2);
                  min-height: rem(50);
                  max-height: rem(50);
                  height: 100%;
                  line-height: rem(22);
                  background: $white;
                  border: rem(1) solid #c4c4c4;
                  padding-left: rem(18);
                  opacity: 0.6;
                  transition: opacity 0.3s ease;

                  &:hover, &:focus {
                    opacity: 1;
                  }

                  .select2-selection__placeholder {
                    color: #535353;
                    @include estiprotext(16);
                    font-weight: 400;
                  }
                }
              }

              .select2-selection--single {
                .select2-selection__rendered {
                  padding-left: 0;
                }

                .select2-selection__arrow {
                  top: 50%;
                  transform: translateY(-50%);

                  b {
                    border: none;
                    height: auto;
                    top: auto;
                    left: auto;
                    right: rem(5);
                    margin: 0;
                    position: relative;
                    width: auto;
                    font-family: icomoon !important;
                    speak: none;
                    font-style: normal;
                    font-weight: 400;
                    font-variant: normal;
                    text-transform: none;
                    line-height: 1;
                    -webkit-font-smoothing: antialiased;

                    &:before {
                      content: "\e903";
                      font-size: rem(8);
                      color: $orange;
                    }
                  }
                }
              }
            }

            &_password {
              position: relative;

              &_control {
                position: absolute;
                top: 45%;
                right: rem(15);
                display: inline-block;

                &_top {
                  top: 25%;
                }

                .icon-eye_input {
                  &:before {
                    color: $orange;
                    font-size: rem(14);
                  }
                }

                &.view {

                  &:before {
                    position: absolute;
                    height: rem(1);
                    width: 100%;
                    background-color: black;
                    transform: rotate(75deg);
                  }
                }
              }
            }

            .invalid-feedback {
              position: absolute;
              left: 99%;
              top: 50%;
              transform: translateY(-50%);
              width: rem(140);
              height: rem(34);
              background: #ff0000;
              color: $white;
              align-items: center;
              justify-content: center;
              margin: 0;
              z-index: 5;

              &:after {
                content: '';
                position: absolute;
                right: 100%;
                top: 50%;
                transform: translateY(-50%);
                border: 17px solid transparent;
                border-right-color: #ff0000;
              }
            }

            &_datepicker {
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              right: rem(13);

              span {
                &:before {
                  color: $orange;
                }
              }
            }
          }

          &_file {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            height: rem(130);
            border-radius: rem(2);
            border: rem(1) solid #c4c4c4;
            color: #535353;
            opacity: 0.6;
            position: relative;

            &:hover {
              opacity: 1;
            }

            &.dragover {
              background-color: #fafafa;
              outline-offset: -17px;
            }

            label {
              margin: 0;
              position: absolute;
              width: 100%;
              height: 100%;
              top: 0;
              left: 0;

              &.focus {
                outline: 1px solid #0078d7;
                outline: -webkit-focus-ring-color auto 5px;
              }

              &:hover {
                cursor: pointer;
              }
            }

            .orange {
              display: block;
              text-align: center;
              color: $orange;
            }

            .icon-upload {
              &:before {
                font-size: rem(34);
              }
            }

            input[type=file] {
              width: 0.1px;
              height: 0.1px;
              opacity: 0;
              position: absolute;
              z-index: -10;
              border: none;

              &:focus {
                outline: none;
              }

              &:focus + label {
                outline: none;
              }
            }
          }

          input {
            display: block;
            width: 100%;
            min-height: rem(50);
            max-height: rem(50);
            height: 100%;
            line-height: rem(22);
            background: $white;
            border: rem(1) solid #c4c4c4;
            box-sizing: border-box;
            border-radius: rem(2);
            padding-left: rem(18);
            color: #535353;
            @include estiprotextbook(16);
            font-weight: 400;
            opacity: 0.6;

            &:hover, &:focus {
              opacity: 1;
            }

            &.invalid {
              border-color: #dc3545;
            }
          }

          &_second {
            padding: 0 rem(13);
            padding-right: rem(28);

            &_group {
              margin: 0;

              .form-check {
                display: flex;
                align-items: center;
                padding-top: rem(4);
                padding-left: rem(27);

                .custom-control-label::before {
                  border-radius: rem(4);
                  border: rem(1) solid #c4c4c4;
                  background-color: $white;
                  width: rem(18);
                  height: rem(18);
                  left: rem(-24);
                  top: 50%;
                  transform: translateY(-50%);
                }

                .custom-control-label::after {
                  left: rem(-24);
                  top: rem(4);
                  width: rem(20);
                  height: rem(20);
                  left: rem(-25);
                  top: 50%;
                  transform: translateY(-50%);
                }

                .custom-control-input:checked ~ .custom-control-label::before {
                  background-color: $orange;
                  width: rem(18);
                  height: rem(18);

                }

                &:checked {
                  color: $orange;
                }

                &-input {
                  max-width: rem(20);
                  width: 100%;
                  min-height: rem(20);
                  height: 100%;
                  position: relative;
                  margin: 0;
                  border-radius: rem(2);
                  border: rem(1) solid #c4c4c4;
                  background-color: #ffffff;

                  label {
                    &:before {
                      background-color: transparent;
                      width: rem(21);
                      height: rem(21);
                      border: rem(1) solid $white;
                    }
                  }
                }

                &-label {
                  padding-left: rem(10);
                }
              }

              label {
                padding-left: rem(5);
                @include estiprotextbook(15);
                color: rgba(83, 83, 83, 0.6);
                font-weight: 400;
              }

              &_control {
                border-radius: rem(6);
                background-color: $gray;
                display: block;
                width: 100%;
                height: rem(44);
                padding: .375rem .75rem;
                color: #25252f;
                @include estiprotext(16);
                font-weight: 300;
                line-height: 1.5;
                border: none;
              }

              &_btn {
                margin-left: auto;
                max-width: rem(210);
                width: 100%;

                .btn {

                  &:hover, &:focus {
                    box-shadow: 0 4px 10px rgba(255, 144, 0, 0.91);
                    color: $white;
                  }
                }

              }
            }

            .btn {
              color: $white;
              @include estiprotext(16);
              font-weight: 400;
              min-height: rem(40);
              max-height: rem(40);
              height: 100%;
              border-radius: rem(20);
              width: 100%;
            }
          }
        }
      }
    }

    &_right {
      height: 100%;
    }

    &_two {
      background-color: #bae3f9;
      display: flex;
      align-items: center;
      box-shadow: 0 rem(2) rem(5) rgba(0, 0, 0, 0.14);
      border-radius: rem(10);
      overflow: hidden;

      &_left {
        background-color: $white;
        padding: rem(45) rem(27.5) rem(30);
        width: rem(830);
      }

      &_right {
        height: 100%;
      }
    }

    &_page {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: $white;
      padding: rem(75) 0 rem(95);

      &_left, &_right {
        width: 42%;

        &_header {
          padding: rem(30) 0;
          max-width: rem(230);
          margin: auto;
          width: 100%;
          text-align: center;

          h3 {
            color: #535353;
            @include estiprotextbook(20);
            font-weight: 400;
          }
        }

        &_block {
          max-width: rem(250);
          width: 100%;
          height: rem(200);
          border-radius: rem(2);
          border: rem(1) solid #c4c4c4;
          background-color: #bae3f9;
          margin: auto;
          display: flex;
          justify-content: flex-end;

          img {
            height: 100%;
          }
        }

        &_btn {
          padding: rem(30) 0;

          .btn {
            margin: auto;
            max-width: rem(210);
            width: 100%;
            max-height: rem(40);
            height: 100%;
            border-radius: rem(20);
            background-color: $orange;
            @include estiprotextbook(16);
            font-weight: 400;
            color: $white;

            &:hover, &:focus {
              color: $white;
              box-shadow: 0 4px 10px rgba(255, 144, 0, 0.91);
            }
          }
        }
      }
    }
  }
}

