@import "vars/variables";
@import "vars/functions";
@import 'bootstrap.min.css';
@import 'bootstrap-grid.min.css';
@import 'select2.min.css';
@import 'fonts';
@import 'header';
@import 'header-index';
@import 'content/content-top';
@import 'content/content-hot-tours';
@import 'content/content-spring-break';
@import 'content/content-add';
@import 'content/content-registr';
@import 'content/content-profile';
@import 'footer';
@import "breakpoint";
@import "responsive";
@import '~js-datepicker/dist/datepicker.min.css';

.bg-container {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;

  label {
    width: 30px;
    height: 30px;
    background: black;
    position: absolute;
    cursor: pointer;
  }

  input {
    display: none;
  }
}

body {
  font-size: #{$browser-context}px;
  font-family: $estiprotext;
  color: $black;
  overflow-x: hidden;

  &.modal-show, &.menu-show {
    overflow: hidden;
  }
}

.container {
  max-width: 1170px;
}

.btn {
  overflow: hidden;
  color: $black-second;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 40px;
  max-height: 40px;
  height: 100%;
  border-radius: rem(20);
  background: transparent;
  transition: all .3s ease;

  &.filled {
    background: $blue;
  }

  &:hover, &:focus {
    color: $black;
    background: $orange;
  }

  &.left {
    text-align: left;
  }

  & + & {
    margin-left: rem(10);
  }

  &__feedback {
    padding: 1.05rem 0.8125rem;

    i {
      font-size: rem(20);
      color: $black;
      margin-right: .65rem;
    }

    i, span {
      vertical-align: middle;
    }

    &:hover, &:focus {
      i {
        color: $black;
      }
    }
  }
}

.btn-blue {
  background-color: $blue;

  &:hover, &:focus {
    background-color: $orange;
  }
}

.btn-orange {
  background-color: $orange;
  color: $white;

  &:hover, &:focus {
    background-color: $orange;
    color: $white;
    box-shadow: 0 4px 10px rgba(255,144,0,.91);
  }
}

.btn-gray {
  background-color: #ebebeb;

  &:hover, &:focus {
    background-color: $orange;
    color: $white;
  }
}
.btn-purple{
  background-color: $purple;

  &:hover, &:focus {
    background-color: $purple;
    color: $white;
  }
}
.text-left {
  text-align: left !important;
}

.text-right {
  text-align: right !important;
}

.text-center {
  text-align: center !important;
}

.float-left {
  float: left !important;
}

.float-right {
  float: right !important;
}

.pull-left {
  float: left !important;
}

.pull-right {
  float: right !important;
}

.full-height {
  height: 100%;
}

.no-padding {
  padding: 0 !important;
}

.w-100 {
  width: 100% !important;
}

.h-100 {
  height: 100% !important;
}

* {
  margin: 0;
  padding: 0;
}

html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.overflow-hidden {
  overflow: hidden !important;
}

.col, .col-1, .col-10, .col-11, .col-12, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-auto, .col-lg, .col-lg-1, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-auto, .col-md, .col-md-1, .col-md-10, .col-md-11, .col-md-12, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-auto, .col-sm, .col-sm-1, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-auto, .col-xl, .col-xl-1, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-auto {
  padding-left: rem(15);
  padding-right: rem(15);
}

.row {
  margin-left: rem(-15);
  margin-right: rem(-15);

  &.no-gutters {
    margin-left: 0;
    margin-right: 0;

    & > .col, & > [class*=col-] {
      padding-left: 0;
      padding-right: 0;
    }
  }

  &.no-parent {
    & > .col, & > [class*=col-] {
      position: static;
    }
  }
}

.row, .d-flex {
  .pull-left {
    margin-right: auto;
  }

  .pull-right {
    margin-left: auto;
  }
}

img {
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6, p {
  font-weight: 400;
  margin: 0;
}

a {
  color: $black;
  text-decoration: none;
  transition: all .3s ease;

  &:hover, &:focus {
    color: $black;
    text-decoration: none;
    transition: all .3s ease;
  }
}

.content{
  background-color: #f2f4f7;
}

::-webkit-input-placeholder {color:#535353;opacity: 0.6}
::-moz-placeholder          {color:#535353;opacity: 0.6}/* Firefox 19+ */
:-moz-placeholder           {color:#535353;opacity: 0.6}/* Firefox 18- */
:-ms-input-placeholder      {color:#535353;opacity: 0.6}
