
.content_top {
  width: 100%;
  height: 100%;
  position: relative;

  &_img {
    height: 100%;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &_info{
    position: absolute;
    z-index: 5;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    &_block {
      position: relative;
      margin-right: rem(75);
      &:before{
        content: "";
        position: absolute;
        width: 4%;
        height: rem(263);
        top: rem(20);
        bottom: rem(30);
        left: 0;
        border-bottom: rem(5) solid $orange-second;
        border-left: rem(5) solid $orange-second;
        border-top: rem(5) solid $orange-second;
        border-bottom-left-radius: rem(15);
        border-top-left-radius: rem(15);
        z-index: 1;
      }
      &:after{
        content: "";
        bottom: rem(30);
        right: 0;
        position: absolute;
        width: 52%;
        height: 32%;
        border-bottom: rem(5) solid $orange-second;
        border-right: rem(5) solid $orange-second;
        border-bottom-right-radius: rem(15);
        z-index: 1;
      }
      &_header{
        position: relative;
        z-index: 2;
        padding-left: rem(35);
        h1{
          @include estiprotext(63);
          font-weight: 700;
          line-height: rem(68);
          color: $white;
          margin-bottom: rem(20);
        }
      }
      &_content{
        position: relative;
        z-index: 2;
        padding: rem(20) rem(30) 0;
        &_group{
          position: relative;
          .form-control{
            padding-left: rem(40);
            height: rem(55);
            color: $black;
          }
          &_icon{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: rem(17);
            margin: 0;
            span{
              &:before{
                color: $orange;
                font-size: rem(15);
              }
            }
          }
        }
      }
      &_bottom{
        position: relative;
        z-index: 2;
        padding-left: rem(30);
        padding-top: rem(5);
        .btn{
          max-width: rem(275);
          width: 100%;
          min-height: rem(60);
          border-radius: rem(30);
          color: #f8f8f8;
          @include estiprotext(18);
          font-weight: 500;
          &:hover, &:focus{
            box-shadow: 0 4px 10px rgba(255, 144, 0, 0.91);
            color: $white;
          }
        }
      }
    }
  }

  &_indicators{
    right: auto;
    bottom: rem(28);
    li{
      border-radius: 50%;
      width: rem(15);
      height: rem(15);
      background-color: $white;
      opacity: 1;
      margin-right: rem(4);
      margin-left: rem(4);
      &.active{
        background-color: $orange-second;
      }
    }
  }
}
.content_tours_pages{
  padding-top: rem(80);
}
.content_top_tours_pages{
  padding-top: rem(20);
  background: url("../img/tours-page-top.png") no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  &_form{
    &_row{
      &_child{
        padding: 0 rem(2.5);
        .content_top_tours_pages_form_row_child_group{
          padding: 0 rem(2.5);
          margin-bottom: rem(5);
        }
        &_group{
          label{
            @include estiprotext(16);
            font-weight: 300;
            color: $white;
          }
          &_control{
            border-radius: rem(6);
            background-color: $gray;
            display: block;
            width: 100%;
            height: rem(44);
            padding: .375rem .75rem;
            color: #25252f;
            @include estiprotext(16);
            font-weight: 300;
            line-height: 1.5;
            border: none;
          }
        }
      }
      &_group{
        flex: 0 0 20%;
        max-width: 20%;
        margin-bottom: rem(5);
        label{
          @include estiprotext(16);
          font-weight: 300;
          color: $white;
        }
        &_control{
          border-radius: rem(6);
          background-color: $gray;
          display: block;
          width: 100%;
          height: rem(44);
          padding: .375rem .75rem;
          color: #25252f;
          @include estiprotext(16);
          font-weight: 300;
          line-height: 1.5;
          border: none;
          &_select{
            position: relative;
            &:before{
              content: '';
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              right: 0;
              width: rem(15);
              height: rem(6);
              background: url("../img/icon/icon-bottom-black.png") no-repeat;
              background-color: $gray;
            }
            .select2-container{
              &--default{
                &.select2-container--open{
                  .select2-selection--single{
                    .select2-selection__arrow{
                      b{
                        transform: rotate(180deg);
                      }
                    }
                  }
                }
                .select2-selection--single{
                  height: rem(44);
                  display: flex;
                  align-items: center;
                  border-radius: rem(6);
                  background-color: $gray;
                  border: none;
                  .select2-selection__placeholder{
                    color: #25252f;
                    @include estiprotext(16);
                    font-weight: 300;
                    line-height: 1.5;
                    opacity: 1;
                  }
                }
              }
              .select2-selection--single{
                .select2-selection__rendered{
                  padding-left: rem(15);
                }
                .select2-selection__arrow{
                  top: 50%;
                  transform: translateY(-50%);
                  b{
                    border: none;
                    width: rem(7);
                    height: rem(6);
                    background: url(../img/icon/icon-bottom-black.png) no-repeat;
                  }
                }
              }
            }
          }
        }
      }
      &_second{
        &_group{
          flex: 0 0 rem(175);
          max-width: rem(175);
          .form-check{
            display: flex;
            align-items: center;
            padding: rem(20) 0;
            &-input{
              max-width: rem(20);
              width: 100%;
              min-height: rem(20);
              height: 100%;
              position: relative;
              margin: 0;
              border-radius: rem(4);
              background-color: #ebebeb;
            }
            &-label{
              padding-left: rem(10);
            }
          }
          label{
            @include estiprotext(16);
            font-weight: 300;
            color: $white;
          }
          .form-check{
            display: flex;
            align-items: center;
            padding-top: rem(4);
            padding-left: rem(27);
            .custom-control-label::before{
              border-radius: rem(4);
              border: rem(1) solid #c4c4c4;
              background-color: $white;
              width: rem(18);
              height: rem(18);
              left: rem(-24);
              top: 50%;
              transform: translateY(-50%);
            }
            .custom-control-label::after{
              left: rem(-24);
              top: rem(4);
              width: rem(20);
              height: rem(20);
              left: rem(-25);
              top: 50%;
              transform: translateY(-50%);
            }
            .custom-control-input:checked~.custom-control-label::before{
              background-color: $orange;
              width: rem(18);
              height: rem(18);

            }
            &:checked{
              color: $orange;
            }
            &-input{
              max-width: rem(20);
              width: 100%;
              min-height: rem(20);
              height: 100%;
              position: relative;
              margin: 0;
              border-radius: rem(2);
              border: rem(1) solid #c4c4c4;
              background-color: #ffffff;

              label {
                &:before {
                  background-color: transparent;
                  width: rem(21);
                  height: rem(21);
                  border: rem(1) solid $white;
                }
              }
            }
            &-label{
              padding-left: rem(10);
            }
          }
          &_control{
            border-radius: rem(6);
            background-color: $gray;
            display: block;
            width: 100%;
            height: rem(44);
            padding: .375rem .75rem;
            color: #25252f;
            @include estiprotext(16);
            font-weight: 300;
            line-height: 1.5;
            border: none;
          }
          &_btn{
            margin: 0 0 rem(35) auto;
            max-width: rem(225);
            width: 100%;
            .btn{

              &:hover, &:focus{
                box-shadow: 0 4px 10px rgba(255, 144, 0, 0.91);
                color: $white;
              }
            }

          }
        }
        .btn{
          color: $white;
          @include estiprotext(18);
          font-weight: 500;
          min-height: rem(52);
          max-height: rem(52);
          height: 100%;
          border-radius: rem(26);
          width: 100%;
        }
      }
    }
  }
}

.content_top_info_block_content_group .select2-container--default{
  &.select2-container--open{
    b{
      transform: rotate(180deg);
      left: 50% !important;
      top: 19px;
      transition: all 0.3s ease;
    }
  }
  .select2-selection--single{
    min-height: rem(55);
    display: flex;
    align-items: center;
    &:before{
      content: '\e91f';
      font-family: icomoon!important;
      font-size: rem(14);
      color: $orange;
      position: absolute;
      top: 45%;
      transform: translateY(-50%);
      left: rem(15);
      width: rem(14);
      height: rem(14);
    }
    .select2-selection__arrow{
      top: 50%;
      transform: translateY(-50%);
      b{
        transition: all 0.3s ease;
        border: none;
        left: 0;
        position: absolute;
        font-family: icomoon!important;
        &:before{
          content: '\e903';
          position: absolute;
          top: 0;
          font-size: rem(5);
        }
      }
    }
  }
}
.select2-results__option{
  padding:  rem(15) rem(23);
  padding-right: 0;
}
.select2-container--default{
  .select2-results__option--highlighted[aria-selected]{
    background-color: rgba(232, 232, 232, 0.5);
    color: $orange;
  }
}
.select2-container--default .select2-selection--single .select2-selection__placeholder{
  color: #25252f;
  opacity: 0.6;
}
.select2-container .select2-selection--single .select2-selection__rendered{
  padding-left: rem(45);
}
.select2-container--default .select2-selection--single .select2-selection__rendered{
  color: #25252f;
}