
.content {
  &_tours {
    padding: rem(5) 0 rem(45);
    background-color: #f2f4f7;

    &_header {
      border-bottom: rem(1) solid $orange;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      margin: rem(40) 0 rem(10);

      &_left {
        padding: rem(10) 0;

        h2 {
          @include estiprotextbook(28);
          font-weight: 400;
          color: $orange-second;
        }
      }

      &_right {
        padding: rem(10) 0;

        a {
          color: $gray-second;
          @include estiprotext(15);
          font-weight: 400;
          border-bottom: rem(1) solid $orange;
          padding: rem(12) 0;

          &:hover, &:focus {
            color: $orange;
          }
        }
      }
    }

    &_pad {
      padding: rem(11) 0;
    }

    &_block {
      display: flex;
      padding: rem(10);
      padding-right: rem(15);
      background-color: $white;
      box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.14);
      border-radius: rem(10);

      &_left {
        position: relative;
        max-width: rem(159);
        width: 100%;

        img {
          width: 100%;
          height: 100%;
        }

        &_absol {
          position: absolute;
          top: rem(-8);
          left: rem(-8);
          background-color: $white;
          border-radius: 50%;
          width: rem(26);
          height: rem(26);
          display: flex;
          align-items: center;
          justify-content: center;

          &:hover {
            .content_tours_block_left_absol_child {
              opacity: 0;
            }

            .content_tours_block_center_date_block_popup {
              opacity: 1;
              z-index: 1;
            }
          }

          &_child {
            position: relative;
            z-index: 2;
            width: 100%;
            height: 100%;
            min-width: rem(16);
            min-height: rem(20);
            border: 0;
            background: transparent;
            cursor: pointer;
            display: flex;
            &_img {
              position: relative;
              &:before{
                color: $orange;
                background-color: $white;
              }
            }

            &:focus {
              outline: none;
            }
          }

          &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background: url("../img/icon/star-icon-min-active.png") no-repeat;
            width: rem(16);
            height: rem(16);
          }
        }
      }

      &_center {
        padding: 0 rem(15);
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &_title {
          display: flex;

          h3 {
            color: $orange;
            @include estiprotext(20);
            font-weight: 500;
          }

          &_sale {
            padding-left: rem(10);
            display: flex;
            align-items: center;
            color: $green;

            span{
              &:before{
                font-size: rem(17);
              }
            }

            p {
              @include estiprotextbook(15);
              font-weight: 400;
              margin-left: rem(10);
            }
          }
        }

        &_navigation {
          display: flex;
          padding: rem(10) 0 rem(15);
          align-items: center;

          p {
            color: #555555;
            @include estiprotextbook(16);
            font-weight: 400;
          }

          span {
            padding: 0 rem(10);
            &:before{
              color: $orange;
              font-size: rem(14);
            }
          }
        }

        &_info {
          padding-bottom: rem(40);

          p {
            color: $gray-second;
            @include estiprotextbook(15);
            font-weight: 300;
          }
        }

        &_date {
          display: flex;
          align-items: center;

          &_block {
            display: flex;
            align-items: center;
            padding-right: rem(35);

            &_img {
              position: relative;
              display: flex;

              span{
                &:before{
                  color: $orange;
                  background-color: $white;
                  font-size: rem(14);
                }
              }

              .icon-calendar, .icon-time{
                &:before{
                  font-size: rem(17);
                }
              }
              .icon-wallet{
                &:before{
                  font-size: rem(16);
                }
              }

              img + img{
                padding-left: rem(2);
              }

              &:hover {
                .content_tours_block_center_date_block_popup {
                  opacity: 1;
                  z-index: 1;
                }
              }
            }

            p {
              color: $gray-second;
              @include estiprotextbook(15);
              font-weight: 400;
              margin-left: rem(10);
            }

            &_popup {
              transition: all 0.5s ease;
              z-index: -1;
              opacity: 0;
              position: absolute;
              top: 100%;
              left: 100%;
              background: #707070;
              border-radius: rem(2);
              padding: rem(7) rem(15);

              p {
                color: $white;
                margin: 0;
                white-space: nowrap;
              }
            }
          }
        }
      }

      &_right {
        max-width: rem(180);
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        width: 100%;
        padding: rem(5) 0;
        margin-left: auto;

        h4 {
          color: $gray-second;
          @include estiprotextbook(16);
          font-weight: 400;
          text-align: right;
        }

        &_price {
          display: flex;
          align-items: center;
          justify-content: flex-end;
          padding: rem(10) 0 rem(5);

          &_old {
            position: relative;

            &:before {
              content: "";
              position: absolute;
              top: 50%;
              left: rem(-5);
              transform: translateY(-50%);
              width: rem(63);
              height: rem(1);
              background-color: $black;
              transform: rotate(-6deg);
            }

            p {
              opacity: 0.8;
              color: #ff0000;
              @include estiprotextbook(15);
              font-weight: 400;
            }
          }

          &_main {
            padding-left: rem(10);

            p {
              color: $orange;
              @include estiprotext(18);
              font-weight: 500;
            }
          }
        }

        &_calculated {
          width: 100%;

          p {
            opacity: 0.6;
            color: $gray-second;
            @include estiprotextbook(15);
            font-weight: 400;
            text-align: right;
          }
        }

        &_btn {
          display: flex;
          align-items: flex-end;
          justify-content: flex-end;

          .btn {
            max-width: rem(126);
            min-width: rem(126);
            width: 100%;
            @include estiprotextbook(15);
            font-weight: 400;
            color: $gray-second;
            transition: all .5s ease;
          }
        }
      }
    }

    &_pages {
      &_header {
        &_right {
          padding: rem(10) 0;
          position: relative;

          &:hover {
            .content_tours_pages_header_right_popup {
              opacity: 1;
              z-index: 1;
            }
          }

          &_popup {
            transition: all .5s ease;
            z-index: -1;
            opacity: 0;
            position: absolute;
            top: 95%;
            right: 0;
            background: $white;
            border-radius: rem(2);
            padding: 0;
            width: rem(176);

            &_item {
              display: block;
              color: $gray-second;
              @include estiprotext(15);
              font-weight: 400;
              padding: rem(10) 0;
              padding-left: rem(22);
              opacity: .7;

              &:hover, &:focus {
                background-color: #e8e8e8;
                opacity: 0.5;
              }
            }
          }
        }
      }
    }
  }

  &_joint_tours {
    padding: rem(45) 0;

    &_block {
      display: flex;
      padding: rem(10);
      padding-right: rem(15);
      background-color: $white;
      box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.14);
      border-radius: rem(10);

      &_left {
        position: relative;
        max-width: rem(159);
        min-width: rem(159);
        width: 100%;

        img {
          width: 100%;
          height: 100%;
        }
      }

      &_center {
        padding: 0 rem(15);
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &_navigation {
          display: flex;
          align-items: center;
          flex-wrap: wrap;

          span {
            display: flex;
            &:before{
              font-size: rem(15);
              color: $orange;
            }
          }

          p {
            padding-left: rem(5);
            padding-right: rem(8);
            color: $orange;
            @include estiprotext(20);
            font-weight: 500;
          }
        }

        &_who {
          display: flex;
          padding: rem(10) 0 rem(15);
          align-items: center;

          p {
            color: #555555;
            @include estiprotextbook(16);
            font-weight: 400;
          }

          span {

          }
        }

        &_date {
          display: flex;
          align-items: center;

          &_block {
            display: flex;
            align-items: center;
            padding-right: rem(35);

            .content_tours_block_center_date_block_popup {
              p {
                color: #fff;
                margin: 0;
                white-space: nowrap;
              }
            }

            p {
              color: $gray-second;
              @include estiprotextbook(15);
              font-weight: 400;
              margin-left: rem(10);
            }
          }
        }
      }

      &_right {
        max-width: rem(180);
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        width: 100%;
        padding: rem(5) 0;
        margin-left: auto;

        &_calculated {
          width: 100%;

          p {
            opacity: 0.6;
            color: $gray-second;
            @include estiprotextbook(15);
            font-weight: 400;
            text-align: right;
          }
        }

        &_btn {
          display: flex;
          align-items: flex-end;
          justify-content: flex-end;

          .btn {
            max-width: rem(126);
            min-width: rem(126);
            width: 100%;
            @include estiprotextbook(15);
            font-weight: 400;
            color: $gray-second;
          }
        }
      }
    }
  }
}

.content_tours_pages_header_right {
  &_url {
    opacity: 0.7;
    color: $gray-second;
    @include estiprotextbook(14);
    font-weight: 400;
    border-bottom: rem(1) solid $orange;
    padding: rem(12) 0;
    span{
      padding-left: rem(5);
      &:before{
        font-size: rem(12);
        color: $gray-second;
        opacity: 0.7;
      }
    }
  }
}

.content_tours_pages_paginations {
  border-top: rem(1) solid $orange;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  padding: rem(10) 0;
  margin-top: rem(20);

  &_nav {
    &_ul {
      margin: 0;

      &_page-item {
        &.active {
          .page-link {
            border-radius: rem(3);
            background-color: $orange-second;
            border: 0;
          }
        }

        &-link {
          cursor: auto;
          background-color: transparent;
          border: 0;
          color: #2b2b2b;
          @include estiprotextbook(15);
          font-weight: 400;

          &:hover {
            color: $orange-second;
            text-decoration: none;
            background-color: transparent;
            border: 0;
          }

          &:focus {
            box-shadow: none;
          }
        }
      }
    }
  }
}

.page-item.disabled .page-link{
  background-color: transparent;
}