.add-page {
  padding-top: rem(80);
  background-color: #f3f4f8;

  &_header {
    padding: rem(35) 0 rem(5);
    border-bottom: rem(1) solid $orange;

    h2 {
      color: #ff9000;
      @include estiprotextbook(24);
      font-weight: 400;
    }
  }

  &_pad {
    padding: rem(20) 0 rem(40);
  }

  &_content {
    background-color: $white;
    box-shadow: 0 rem(2) rem(5) rgba(0, 0, 0, 0.14);
    border-radius: rem(10);
    padding: rem(45) rem(27.5) rem(30);

    .needs-validation {
      max-width: rem(830);
      margin: auto;
    }

    &_form {
      width: 100%;

      &_row {
        width: 100%;
        align-items: center;

        .mb-3 {
          margin-bottom: rem(20) !important;
          padding: 0 rem(17.5) !important;
        }

        &_input {
          position: relative;
          display: flex;
          align-items: center;

          label {
            color: #535353;
            font-weight: 400;
            @include estiprotextbook(16);
            white-space: nowrap;
            margin: 0 rem(35) 0 auto;
            text-align: right;
          }

          input {
            max-width: rem(635);
          }

          &:hover {
            span {
              path {
                fill: $orange-second;
              }
            }
          }

          .select2-container {
            &--default {
              &.select2-container--open {
                .select2-selection--single {
                  .select2-selection__arrow {
                    b {
                      transform: rotate(180deg);
                    }
                  }
                }
              }

              .select2-selection--single {
                display: flex;
                align-items: center;
                border-radius: rem(2);
                min-height: rem(50);
                max-height: rem(50);
                height: 100%;
                line-height: rem(22);
                background: $white;
                border: rem(1) solid #c4c4c4;
                padding-left: rem(18);
                transition: opacity 0.3s ease;

                &:hover, &:focus {
                  opacity: 1;
                }

                .select2-selection__placeholder {
                  color: #535353;
                  @include estiprotext(16);
                  font-weight: 400;
                  opacity: 1;
                }
              }
            }

            .select2-selection--single {
              .select2-selection__rendered {
                padding-left: 0;
              }

              .select2-selection__arrow {
                top: 50%;
                transform: translateY(-50%);

                b {
                  border: none;
                  height: auto;
                  top: auto;
                  left: auto;
                  right: rem(5);
                  margin: 0;
                  position: relative;
                  width: auto;
                  font-family: icomoon !important;
                  speak: none;
                  font-style: normal;
                  font-weight: 400;
                  font-variant: normal;
                  text-transform: none;
                  line-height: 1;
                  -webkit-font-smoothing: antialiased;

                  &:before {
                    content: "\e903";
                    font-size: rem(8);
                    color: $orange;
                  }
                }
              }
            }
          }

          .invalid-feedback {
            position: absolute;
            left: 99%;
            top: 50%;
            transform: translateY(-50%);
            width: rem(140);
            height: rem(34);
            background: #ff0000;
            color: $white;
            align-items: center;
            justify-content: center;
            margin: 0;
            z-index: 5;

            &:after {
              content: '';
              position: absolute;
              right: 100%;
              top: 50%;
              transform: translateY(-50%);
              border: 17px solid transparent;
              border-right-color: #ff0000;
            }
          }

        }

        &_group {
          position: relative;
          display: flex;
          text-align: right;

          &_label {
            width: 20%;
            padding-top: rem(12);

            label {
              color: #535353;
              font-weight: 400;
              @include estiprotextbook(16);
              white-space: nowrap;
              margin: 0 rem(30) 0 auto;
            }

            p {
              color: #535353;
              font-weight: normal;
              @include estiprotextbook(13);
              margin: rem(5) rem(30) 0 auto;
              opacity: 0.4;
            }
          }

          &_check {
            width: 80%;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
          }

          &_route {
            width: 37%;

            .form-control {
              max-width: 100%;
            }

            .select2-container--default
            .select2-selection--single {
              border-top-right-radius: 0;
              border-bottom-right-radius: 0;
            }

            &_dash {
              width: 3%;
              height: rem(64);
              margin: 0 rem(17);

              &:before {
                content: '';
                position: absolute;
                width: 100%;
                height: rem(1);
                top: 50%;
                background-color: $black;
                transform: translateY(-50%);
              }
            }

            &_input {
              width: 63%;

              input {
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
              }

              &_btn {
                width: 100%;
                border: rem(1) solid #c4c4c4;
                border-radius: rem(2);

                .btn {
                  border-radius: rem(2);

                  &:hover, &:focus {
                    color: $white;
                  }
                }
              }
            }
          }

          &_text-area {
            width: 80%;

            textarea {
              width: 100%;
              padding: rem(17) rem(15);
              border-radius: rem(2);
              border: solid rem(1) #c4c4c4;
              font-family: GTEestiProText;
              font-size: 16px;
              @include estiprotext(16);
              font-weight: normal;
              color: #535353;
            }

            .max-words {
              position: absolute;
              right: rem(10);
              bottom: rem(10);
              @include estiprotext(13);
              font-weight: normal;
              color: rgba(83, 83, 83, 0.4);
            }
          }

          &_block_upload{
            width: 82%;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            margin-left: rem(-8);
            margin-right: rem(-8);
          }
          &_upload {
            padding: rem(10) rem(8);
            width: 20%;

            &_file {
              display: flex;
              justify-content: center;
              align-items: center;
              height: rem(110);
              width: 100%;
              border-radius: rem(2);
              border: rem(1) solid #c4c4c4;
              color: #535353;
              position: relative;

              .icon-plus {
                font-size: rem(34);
              }
            }
          }

          &_duration {
            input {
              max-width: rem(80);
            }

            label {
              display: flex;
              align-items: center;
              margin: 0 rem(20) 0 rem(6);
            }

            &_second {
              input {
                max-width: rem(165);
              }

              label {
                display: flex;
                align-items: center;
                margin: 0 rem(22) 0 rem(30);
              }
            }
          }

          &_price {
            input {
              max-width: rem(130);
              padding: 0 rem(10);
            }

            label {
              display: flex;
              align-items: center;
              margin: 0 rem(20) 0 rem(6);
            }

            &_second {
              padding-left: rem(20);

              input {
                max-width: rem(120);
                padding: 0 rem(10);
              }
            }

            &_third {
              padding: 0 rem(20);

              input {
                min-width: rem(250);
                padding: 0;
                padding-left: rem(10) !important;
              }

              label {
                display: flex;
                align-items: center;
                margin: 0 rem(22) 0 rem(30);
              }
            }

            &_select {
              max-width: rem(75);

              .select2-container--default
              .select2-selection--single {
                padding-left: rem(10);
              }
            }
          }

          &_acts {
            &_block{
              width: 50%;
            }
            input {
              max-width: rem(80);
            }
            .add-page_content_form_row_group_acts_label{
              margin: 0 rem(13) 0 rem(20);
            }
            label {
              display: flex;
              align-items: center;
              margin: 0 rem(13) 0 rem(5);
            }
          }
        }

        .add-page_content_form_row_group_upload_file {
          input {
            width: 100%;
            min-height: rem(108);
            opacity: 0;
            position: absolute;
            cursor: pointer;
            padding: 0;

            &:hover, &:focus {
              opacity: 0;
            }
          }
        }

        .form-check {
          display: flex;
          align-items: center;
          padding: rem(5) 0 rem(5) rem(27);
          width: 33.33%;

          .custom-control-label::before {
            border-radius: rem(4);
            border: rem(1) solid #c4c4c4;
            background-color: $white;
            width: rem(18);
            height: rem(18);
            left: rem(-24);
            top: 50%;
            transform: translateY(-50%);
          }

          .custom-control-label::after {
            left: rem(-24);
            top: rem(4);
            width: rem(20);
            height: rem(20);
            left: rem(-25);
            top: 50%;
            transform: translateY(-50%);
          }

          .custom-control-input:checked ~ .custom-control-label::before {
            background-color: $orange;
            width: rem(18);
            height: rem(18);

          }

          label {
            color: #535353;
            font-weight: 400;
            @include estiprotextbook(16);
            white-space: nowrap;
            margin: 0;
            margin-right: rem(30);
            cursor: pointer;
          }

          &:checked {
            color: $orange;
          }

          &-input {
            max-width: rem(20);
            width: 100%;
            min-height: rem(20);
            height: 100%;
            position: relative;
            margin: 0;
            border-radius: rem(2);
            border: rem(1) solid #c4c4c4;
            background-color: #ffffff;

            label {
              &:before {
                background-color: transparent;
                width: rem(21);
                height: rem(21);
                border: rem(1) solid $white;
              }
            }
          }

          &-label {
            padding-left: rem(10);
          }
        }

        &_file {
          display: flex;
          justify-content: center;
          align-items: center;
          height: rem(110);
          width: rem(110);
          border-radius: rem(2);
          border: rem(1) solid #c4c4c4;
          color: #535353;
          position: relative;

          &:hover {
            opacity: 1;
          }

          &.dragover {
            background-color: #fafafa;
            outline-offset: -17px;
          }

          label {
            margin: 0;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;

            &.focus {
              outline: 1px solid #0078d7;
              outline: -webkit-focus-ring-color auto 5px;
            }

            &:hover {
              cursor: pointer;
            }
          }

          .icon-plus {
            font-size: rem(34);
          }

          input[type=file] {
            width: 0.1px;
            height: 0.1px;
            opacity: 0;
            position: absolute;
            z-index: -10;
            border: none;

            &:focus {
              outline: none;
            }

            &:focus + label {
              outline: none;
            }
          }
        }

        input {
          display: block;
          width: 100%;
          min-height: rem(50);
          max-height: rem(50);
          height: 100%;
          line-height: rem(22);
          background: $white;
          border: rem(1) solid #c4c4c4;
          box-sizing: border-box;
          border-radius: rem(2);
          padding-left: rem(16);
          color: #535353;
          @include estiprotextbook(16);
          font-weight: 400;

          &:hover, &:focus {
            opacity: 1;
          }

          &.invalid {
            border-color: #dc3545;
          }
        }

        &_second {
          padding: 0 rem(13);
          padding-right: rem(28);

          &_group {
            margin: 0;
            display: flex;

            &_check {

            }

            .form-check {
              display: flex;
              align-items: center;
              padding-top: rem(4);
              padding-left: rem(27);

              .custom-control-label::before {
                border-radius: rem(4);
                border: rem(1) solid #c4c4c4;
                background-color: $white;
                width: rem(18);
                height: rem(18);
                left: rem(-24);
                top: 50%;
                transform: translateY(-50%);
              }

              .custom-control-label::after {
                left: rem(-24);
                top: rem(4);
                width: rem(20);
                height: rem(20);
                left: rem(-25);
                top: 50%;
                transform: translateY(-50%);
              }

              .custom-control-input:checked ~ .custom-control-label::before {
                background-color: $orange;
                width: rem(18);
                height: rem(18);

              }

              &:checked {
                color: $orange;
              }

              &-input {
                max-width: rem(20);
                width: 100%;
                min-height: rem(20);
                height: 100%;
                position: relative;
                margin: 0;
                border-radius: rem(2);
                border: rem(1) solid #c4c4c4;
                background-color: #ffffff;

                label {
                  &:before {
                    background-color: transparent;
                    width: rem(21);
                    height: rem(21);
                    border: rem(1) solid $white;
                  }
                }
              }

              &-label {
                padding-left: rem(10);
              }
            }

            label {
              padding-left: rem(5);
              @include estiprotextbook(15);
              color: rgba(83, 83, 83, 0.6);
              font-weight: 400;
            }

            &_control {
              border-radius: rem(6);
              background-color: $gray;
              display: block;
              width: 100%;
              height: rem(44);
              padding: .375rem .75rem;
              color: #25252f;
              @include estiprotext(16);
              font-weight: 300;
              line-height: 1.5;
              border: none;
            }

            &_btn {
              padding: rem(15) 0 rem(20);
              display: flex;
              align-items: center;
              justify-content: flex-end;
              width: 100%;

              .btn {
                max-width: rem(150);
                &:hover, &:focus {
                  box-shadow: 0 4px 10px rgba(255, 144, 0, 0.91);
                  color: $white;
                }
              }
              .btn + .btn{
                margin-left: rem(22);
              }
              .btn-gray{
                color: $black-second;
                &:hover, &:focus {
                  background-color: $gray;
                  box-shadow: 0 4px 10px rgba(233, 228, 241, 0.91);
                  color: $black-second;
                }
              }
            }
          }

          .btn {
            @include estiprotext(16);
            font-weight: 400;
            min-height: rem(40);
            max-height: rem(40);
            height: 100%;
            border-radius: rem(20);
            width: 100%;
          }
        }
      }
    }
  }
}