.header_other {
  background-color: $orange;

  .header_navbar_collapse_nav_item {
    border-bottom: rem(5) solid $orange;

    &.active {
      border-bottom: rem(5) solid $white;
      background-color: rgba(255, 255, 255, 0.2);
      padding: 0 rem(13.5);

      .header_navbar_collapse_nav_item_link {
        color: $purple;
      }
    }

    &:hover {
      border-bottom: rem(5) solid $white;
    }
  }

  .header_navbar_collapse_nav_item_pad {
    padding: rem(20) 0;
  }

  .header_navbar_collapse_nav_item_white {
    &:hover {
      .header_navbar_collapse_nav_item_link {
        color: $purple;
      }
    }
  }

  .header_navbar_collapse_nav_pad-btn,
  .header_navbar_collapse_nav_item_white {
    &:hover {
      border-bottom: rem(5) solid $orange;
    }
  }

  .header_navbar_collapse_nav_item_link {
    &:hover {
      color: #25252f;
    }
  }
}