
body{
  overflow-x: visible !important;
}
.spring-break {
  padding: rem(80) 0 rem(37);
  background-color: #f2f4f7;

  &_header {
    padding-top: rem(40);
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    border-bottom: rem(1) solid $orange;

    &_navigation {
      display: flex;
      align-items: center;

      a {
        opacity: 0.8;
        @include estiprotext(15);
        font-weight: 300;
        color: #34343b;
        padding-right: rem(10);
      }

      span {
        padding-bottom: rem(20);
        transform: rotate(-90deg);

        &:before {
          font-size: rem(7);
        }
      }
    }

    &_right {
      display: flex;
      justify-content: space-between;

      &_text {
        padding-right: rem(12);

        p {
          color: $gray-second;
          @include estiprotext(12);
          padding: rem(12) 0;
          opacity: 0.5;
          font-weight: normal;
        }
      }

      &_url {
        display: flex;
        align-items: center;
        border-bottom: rem(1) solid $orange;

        &_item {
          display: flex;
          padding: rem(8);
          position: relative;

          span {
            &:hover:before {
              opacity: 1;
              color: $orange;
            }

            &:before {
              color: rgba(0, 0, 0, 0.3);
            }
          }

          .icon-eye_view {
            font-size: rem(11);
          }

          .icon-add {
            font-size: rem(18);
          }

          .icon-social {
            font-size: rem(17);
          }

          &:hover {
            color: $orange;
            .spring-break_header_helpers_popup{
              opacity: 1;
              z-index: 1;
            }
          }
        }

        .header_navbar_lang_menu_dropdown_item{
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }

    &_helpers {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
        .spring-break_header_helpers_child {
          opacity: 0;
        }

        .spring-break_header_helpers_popup {
          opacity: 1;
          z-index: 1;
        }
      }

      &_child {
        position: relative;
        z-index: 2;
        width: 100%;
        height: 100%;
        min-width: rem(18);
        min-height: rem(18);
        border: 0;
        background: transparent;
        cursor: pointer;
        display: flex;
        &_img {
          position: relative;
          &:before{
            background-color: #f2f4f7;
          }
        }

        &:focus {
          outline: none;
        }
      }

      &:before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url("../img/icon/star-icon-min-active.png") no-repeat;
        width: rem(18);
        height: rem(18);
      }

      &_popup {
        transition: all 0.5s ease;
        z-index: -1;
        opacity: 0;
        position: absolute;
        top: 100%;
        right: 100%;
        background: #707070;
        border-radius: rem(2);
        padding: rem(7) rem(15);

        p {
          color: $white;
          margin: 0;
          white-space: nowrap;
        }
      }
    }
  }

  &_pad {
    padding: rem(20) 0 rem(45);
  }

  &_content {
    padding: rem(10);
    padding-right: rem(15);
    background-color: $white;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.14);
    border-radius: rem(10);
    position: relative;

    &_left {
      width: 82%;
      &_top {
        display: flex;

        &_carousel {
          position: relative;
          min-width: rem(350);
          width: 100%;

          .carousel-control-prev {
            opacity: 1;
            .icon-arrow_down {
              transform: rotate(90deg);
            }
          }

          .carousel-control-next {
            opacity: 1;
            .icon-arrow_down {
              transform: rotate(-90deg);
            }
          }

          .icon-arrow_down {
            &:before {
              font-size: rem(12);
            }
          }
        }

        &_center {
          padding: 0 rem(15);
          display: flex;
          flex-direction: column;
          justify-content: space-between;

          &_title {
            display: flex;

            h3 {
              color: $orange;
              @include estiprotext(20);
              font-weight: 500;
            }

            &_sale {
              padding-left: rem(10);
              display: flex;
              align-items: center;
              color: $green;
              span{
                &:before{
                  font-size: rem(17);
                }
              }

              p {
                @include estiprotextbook(15);
                font-weight: 400;
                margin-left: rem(10);
              }
            }
          }

          &_navigation {
            display: flex;
            padding: rem(10) 0 rem(15);
            align-items: center;

            p {
              color: #555555;
              @include estiprotextbook(16);
              font-weight: 400;
            }

            span {
              padding: 0 rem(10);
              &:before{
                font-size: rem(14);
                color: $orange;
              }
            }
          }

          &_info {
            padding-bottom: rem(40);

            p {
              color: $gray-second;
              @include estiprotextbook(15);
              font-weight: 300;
            }
          }
        }
      }

      &_pad {
        padding-left: rem(15);
      }

      &_center {
        border-top: rem(1) solid #c4c4c4;

        &_header {
          display: flex;
          padding: rem(25) 0;

          span {
            &:before {
              font-size: rem(16);
              color: $orange;
            }
          }

          h4 {
            padding-left: rem(20);
            @include estiprotext(16);
            font-weight: normal;
          }
        }

        &_photo {
          display: flex;
          flex-wrap: wrap;
          padding: rem(15) 0;

          &_block {
            padding: rem(11) 0;
            padding-left: rem(26);
            width: 33.333%;

            &_img {
              position: relative;

              &:hover, &:focus {
                &:before, &:after {
                  opacity: 1;
                }
              }

              &:before {
                opacity: 0;
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.6);
                border-radius: rem(3);
                transition: all 0.3s ease;
                cursor: pointer;
              }

              &:after {
                opacity: 0;
                content: '';
                position: absolute;
                width: rem(30);
                height: rem(30);
                top: 50%;
                left: 50%;
                transition: all 0.3s ease;
                transform: translate(-50%, -50%);
                background: url("../img/icon/search.svg") no-repeat;
                cursor: pointer;
              }
            }
          }
        }

        &_info {
          position: relative;
          padding: rem(25) 0;

          p {
            @include estiprotext(16);
            font-weight: 300;
            color: #555555;
            max-width: rem(560);
            width: 100%;
          }

          &_btn {
            position: absolute;
            right: rem(5);
            top: 50%;
            transform: translateY(-50%);
            color: $orange;
            text-align: center;
            &.collapsed{
              .icon-arrow_down{
                transform: rotate(0deg);
              }
            }

            &:hover, &:focus {
              color: $orange;
            }

            p {
              @include estiprotext(16);
              font-weight: normal;
              color: $orange;

              &:hover, &:focus {
                color: $orange;
              }
            }
            span{
              display: block;
              transition: all 0.3s ease;
              transform: rotate(180deg);
              &:before{
                font-size: rem(12);
              }
            }
          }
        }
      }
    }

    &_right {
      max-width: rem(180);
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;
      padding: rem(5) 0;
      margin-left: auto;
      float: right;
      position: sticky;
      top: rem(20);


      h4 {
        color: $gray-second;
        @include estiprotextbook(16);
        font-weight: 400;
        text-align: right;
      }

      &_price {
        text-align: center;
        padding: rem(10) 0 0;

        &_old {
          position: relative;

          &:before {
            content: "";
            position: absolute;
            top: 30%;
            left: 50%;
            width: rem(63);
            height: rem(1);
            background-color: $black;
            transform: rotate(-6deg) translateX(-50%);
          }

          p {
            opacity: 0.8;
            color: #ff0000;
            @include estiprotextbook(15);
            font-weight: 400;
          }
        }

        &_main {

          p {
            color: $orange;
            @include estiprotext(18);
            font-weight: 500;
          }
        }
      }

      &_calculated {
        width: 100%;

        p {
          opacity: 0.6;
          color: $gray-second;
          @include estiprotextbook(15);
          font-weight: 400;
          text-align: center;
        }
      }

      &_btn {
        display: flex;
        justify-content: center;
        padding: rem(10) 0;

        .btn {
          max-width: rem(150);
          min-width: rem(150);
          width: 100%;
          @include estiprotext(18);
          font-weight: normal;
          transition: all .5s ease;
        }
      }
    }
  }
}