.footer {
  background-color: #e6e8f2;
  padding: rem(30) 0 rem(40);

  &_logo {
    &_info {
      padding-top: rem(50);

      p {
        color: #818389;
        @include estiprodisplay(14);
        font-weight: 300;
      }
    }
  }

  &_center {
    display: flex;
    justify-content: space-between;

    &_left, &_right {
      &_title {
        padding-bottom: rem(20);

        &_url {
          @include estiprodisplay(16);
          font-weight: 400;
        }
      }

      &_url {
        display: block;
        color: #2c2e2d;
        @include estiprodisplay(14);
        font-weight: 300;
        line-height: 24px;

        &:hover, &:focus {
          color: $orange;
        }
      }
    }
  }

  &_store {
    &_url {
      a {
        max-width: rem(126);
        width: 100%;
      }

      a + a {
        padding-left: rem(10);
      }
    }

    &_info {
      padding: rem(15) 0;

      p {
        color: #2c2e2d;
        @include estiprodisplay(14);
        font-weight: 300;
      }
    }
  }
}

.send_message {
  position: absolute;

  &_social {
    position: fixed;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    z-index: 5;

    &_block {
      width: rem(48);
      height: rem(48);
      background-color: $purple;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: rem(1);
      span{
        &:before{
          color: $white;
        }
      }
      .icon{
        &-vk{
          font-size: rem(14);
        }
        &-fb{
          font-size: rem(24);
        }
        &-telegram{
          font-size: rem(21);
        }
        &-instagram{
          font-size: rem(26);
        }
      }

      &:hover {
        background-color: $orange;
      }
    }
  }

  &_modal {
    font-family: icomoon !important;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    &:before {
      content: "\e905";
      right: rem(30);
      top: rem(25);
      color: $white;
      position: absolute;
      cursor: pointer;
    }
    &_dialog {
      &_content {
        border-radius: rem(10);
        box-shadow: 0px 2px 32px 0 rgba(0, 0, 0, 0.32);
        background-color: $white;

        &_header {
          display: flex;
          justify-content: center;
          align-items: center;
          padding: rem(35) 0 rem(15);
          border: none;

          .modal-title {
            font-weight: normal;
            @include estiprotext(24);
            color: #535353;
            text-transform: uppercase;
          }
        }

        &_body {
          padding: 0 rem(50);
          border: none;

          &_control {
            padding: rem(10);

            .form-control {
              height: rem(50);
              opacity: 0.6;
              @include estiprotext(16);
              font-weight: normal;
              color: $black;
              border-radius: 2px;
              border: solid rem(1) #c4c4c4;
              &:hover{
                opacity: 1;
                transition: all 0.5s ease;
              }
            }
          }

          &_check {
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: rem(5) rem(10);

            .form-check {
              display: flex;
              align-items: center;
              padding-left: rem(27);

              .custom-control-label::before {
                border-radius: rem(4);
                border: rem(1) solid #c4c4c4;
                background-color: $white;
                width: rem(18);
                height: rem(18);
                left: rem(-24);
                top: 50%;
                transform: translateY(-50%);
              }

              .custom-control-label::after {
                width: rem(20);
                height: rem(20);
                left: rem(-26);
                top: 50%;
                transform: translateY(-50%);
              }

              .custom-control-input:checked ~ .custom-control-label::before {
                background-color: $orange;
                width: rem(18);
                height: rem(18);

              }

              &:checked {
                color: $orange;
              }

              label {
                @include estiprotext(14);
                font-weight: normal;
                text-align: left;
                color: #53535399;

                &:before {
                  background-color: transparent;
                  width: rem(21);
                  height: rem(21);
                  border: rem(1) solid $white;
                }
              }
            }

            &_btn {
              color: $orange;
              @include estiprotext(14);
              font-weight: normal;

              &:hover, &:focus {
                color: $orange;
              }
            }
          }

          &_btn {
            padding: rem(10);

            .btn-orange {
              margin: auto;
              max-height: rem(50);
              min-height: rem(50);
              max-width: rem(282);
              width: 100%;
              border-radius: rem(25);
              @include estiprotext(20);
              font-weight: normal;
              color: $white;
            }

            p {
              margin: rem(30) 0 rem(20);
              text-align: center;
              font-weight: normal;
              @include estiprotext(16);
              color: #535353;
            }

            &_social {
              display: flex;
              align-items: center;
              justify-content: center;

              .btn-fb {
                background-color: #3b5897;

                &:hover, &:focus {
                  background-color: #3b5897;
                }
              }

              .btn-gl {
                background-color: #dc4a3d;

                &:hover, &:focus {
                  background-color: #dc4a3d;
                }
              }

              .btn {
                max-height: rem(45);
                min-height: rem(45);
                max-width: rem(45);
                width: 100%;
                border-radius: 50%;

                span {
                  &:before {
                    color: $white;
                    font-size: rem(23);
                  }
                }
              }
            }
          }
        }

        &_footer {
          display: block;
          text-align: center;
          padding: rem(25) rem(40);
          border-top: rem(1) solid #c4c4c4;

          a {
            @include estiprotext(20);
            font-weight: normal;
            color: $orange;
            margin: rem(5) 0 rem(20);
            &:hover, &:focus{
              color: $orange;
            }
          }

          p {
            opacity: 0.6;
            @include estiprotext(14);
            font-weight: normal;
            color: #53535399;
          }
        }
      }
    }
  }

  &_individ{
