.modal-open {
  padding: 0 !important;
  height: 100vh;
}

.modal-backdrop.show {
  background: rgba(0, 0, 0, 0.75);
}

.popup-carousel {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  text-align: center;
  z-index: 99;
  display: none;

  &_bg {
    background: rgba(0, 0, 0, 0.4);
    position: absolute;
    z-index: 1;
    height: 100%;
    width: 100%;
    font-family: icomoon !important;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    &:before {
      content: "\e905";
      right: rem(30);
      top: rem(25);
      color: $white;
      position: absolute;
      cursor: pointer;
    }
  }

  &_img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    max-height: rem(600);
    max-width: rem(900);
    width: 100%;
    height: 100%;
    box-shadow: 0 rem(2) rem(32) rgba(0, 0, 0, 0.32);
    border-radius: rem(10);
    border: rem(3) solid $white;
    background-color: $white;

    &:hover {
      &:before, &:after,
      .popup-carousel_img_carousel_prev,
      .popup-carousel_img_carousel_next {
        opacity: 1;
      }
    }

    &:before, &:after {
      opacity: 0;
      transition: all .3s ease;
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      z-index: 1;
