      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#000000', endColorstr='#000000', GradientType=1);
    }

    &_carousel {
      height: 100%;

      &_prev, &_next {
        z-index: 2;
        opacity: 0;
        transition: all .3s ease;
      }

      &_prev {
        .icon-arrow_down {
          transform: rotate(90deg);
        }
      }

      &_next {
        .icon-arrow_down {
          transform: rotate(-90deg);
        }
      }

      &_inner {
        height: 100%;

        &_item {
          height: 100%;

          img {
            height: 100%;
            border-radius: rem(10);
          }
        }
      }
    }
  }
}

